<template>
  <div id="reset">
    <el-main>
      <div class="main">
        <div class="login">
          <div class="username input">
            <span>输入旧密码</span>
            <el-input v-model="oldpassword" placeholder="请输入旧密码"></el-input>
          </div>
          <div class="password input">
            <span>输入新密码</span>
            <el-input v-model="newpassword" type="password" placeholder="请输入新密码"></el-input>
          </div>
          <div class="password input">
            <span>再次输入新密码</span>
            <el-input v-model="checkpassword" type="password" placeholder="请输入新密码"></el-input>
          </div>
          <div class="button">
            <el-button type="primary" @click="change">修改</el-button>
          </div>
        </div>
      </div>
    </el-main>
  </div>
</template>


<script>
export default {
  name: "reset",
  data() {
    return {
      oldpassword:"",
      newpassword:"",
      checkpassword:""
    };
  },
  methods: {
    change() {
      if(this.newpassword !== this.checkpassword) {
        this.$message.error('两次密码输入不一致！');
        return;
      }
      const data = {
        oldpassword: this.oldpassword,
        newpassword: this.newpassword
      }
      this.loading = this.$loading();
      this.$axios.admin(data).then( res => {
        this.$router.back();
        this.$message({
          message: '修改成功！',
          type: 'success'
        });
      })
      .catch(this.$message.error)
      .finally(() => {
        this.loading.close();
      })
    }
  },
  created() {
    // this.$loading();
  }
};
</script>

<style scoped lang="scss">
#reset {
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.5);
  position: relative;
}
.main {
  .login {
    width: 400px;
    background-color: #fff;
    border-radius: 5px;
    padding: 20px 10px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .input {
      display: flex;
      align-items: center;
      padding: 20px 10px;
      span {
        flex: 1;
      }
      .el-input {
        flex: 2;
        width: 180px;
        margin: 0 20px;
      }
    }
    .button {
      text-align: center;
    }
  }
}
</style>



