<template>
  <div id="company">
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home/company/1' }" class="first">公司管理</el-breadcrumb-item>
        <el-breadcrumb-item v-if="$route.params.type != '1'">{{nav}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="handle">
      <div class="search" v-if="$route.params.type == 1 || $route.params.type == 6">
        <span>公司名称</span>
        <el-input v-model="search" placeholder="请输入查询内容"></el-input>
        <el-button type="primary" @click="goSearch">查询</el-button>
      </div>
      <div class="select" v-if="$route.params.type == 1">
        <el-radio-group v-model="type" @change="$router.push(`/home/company/${type}`)">
          <el-radio-button label=1>所有公司</el-radio-button>
          <el-radio-button label=2>试用</el-radio-button>
          <el-radio-button label=3>即将到期</el-radio-button>
          <el-radio-button label=4>已过期</el-radio-button>
        </el-radio-group>
      </div>
    </div>
    <div class="table">
      <el-table
        :data="list"
        border
        style="width: 100%">
        <el-table-column
          prop="companyname"
          label="公司名称"
          align="center">
        </el-table-column>
        <el-table-column
          prop="adminusername"
          label="管理员姓名"
          align="center">
        </el-table-column>
        <el-table-column
          prop="adminmobile"
          label="手机号码"
          align="center">
        </el-table-column>
        <el-table-column
          prop="adminemail"
          label="邮箱"
          align="center">
        </el-table-column>
        <el-table-column
          prop="companyexpire"
          label="到期时间"
          :formatter="format"
          align="center">
        </el-table-column>
        <el-table-column
          prop="members"
          label="人员"
          align="center">
          <template slot-scope="scope">
            <span class="handle" @click="dialogChange(scope.row.companyname, 1)">{{ scope.row.members }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="totalhost"
          label="服务器数量"
          align="center">
          <template slot-scope="scope">
            <span class="handle" @click="dialogChange(scope.row.companyname, 2)">{{ scope.row.totalhost }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="remark"
          label="备注"
          align="center">
        </el-table-column>
        <el-table-column
        prop="members"
          label="操作"
          align="center">
          <template slot-scope="scope">
            <el-date-picker
              type="date"
              v-model="scope.row.companyexpire"
              format="yyyy-MM-dd "
              value-format="timestamp"
              @click.native="lastTime = scope.row.companyexpire"
              @change="pickTime(scope.row)"
              :editable="false">
            </el-date-picker>
            <el-button type="text" size="small" @click="edit(scope.$index)">编辑</el-button>
            <el-button type="text" size="small" @click="deleteRow(scope.$index, list)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      layout="prev, pager, next"
      prev-text="上一页"
      next-text="下一页"
      class="pagination"
      :page-count="pagetotal"
      :current-page.sync="page"
      @current-change="change">
    </el-pagination>
    <el-dialog title="编辑公司" :visible.sync="dialogFormVisible">
      <div class="content">
        <div class="company-name item">
          <span>公司名称：</span>
          <el-input v-model="temp.companyname" placeholder="请输入内容" class="name"></el-input>
        </div>
        <div class="company-emai item">
          <span>公司邮箱：</span>
          <el-input v-model="temp.adminemail" placeholder="请输入内容" class="name"></el-input>
        </div>
        <div class="company-emai item">
          <span>备注：</span>
          <el-input
            type="textarea"
            :rows="5"
            placeholder="请输入内容"
            class="name"
            v-model="temp.companymark">
          </el-input>
        </div>
        <div class="company-emai item">
          <span>公司状态：</span>
          <div class="name">
            <el-radio v-model="temp.disable" label="false">停用</el-radio>
            <el-radio v-model="temp.disable" label="true">启用</el-radio>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit">提 交</el-button>
      </div>
    </el-dialog>
    <el-dialog title="公司运维人员" :before-close="closeDialog" :visible.sync="dialog.isShow">
      <div class="content">
        <template v-if="dialog.show === 1">
          <el-table
            :data="dialog.dialogList"
            border
            height="350px"
            style="width: 100%"
            key=1>
            <el-table-column
              prop="userallinfo.username"
              label="姓名"
              align="center">
            </el-table-column>
            <el-table-column
              prop="userallinfo.mobile"
              label="手机号码"
              align="center">
            </el-table-column>
            <el-table-column
              prop="userallinfo.oprole"
              label="权限"
              align="center"
              :formatter="oprole">
            </el-table-column>
            <el-table-column
              prop="userallinfo.userstatus"
              label="是否和账号关联"
              align="center">
              <template slot-scope="scope">
                <span class="userstatus" :style="{color:scope.row.userallinfo.userstatus ==='register'? 'green' : '#606266'}">{{ userstatus(scope.row.userallinfo.userstatus) }}</span>
              </template>
            </el-table-column>
          </el-table>
        </template>
        <template v-if="dialog.show === 2">
          <el-table
            :data="dialog.dialogList"
            border
            height="350px"
            style="width: 100%"
            key=2>
            <el-table-column
              prop="name"
              label="服务器名称"
              align="center">
            </el-table-column>
            <el-table-column
              prop="host"
              label="服务器IP地址"
              align="center">
            </el-table-column>
            <el-table-column
              prop="Hoststatus"
              label="监控服务器"
              align="center">
              <template slot-scope="scope">
                <span class="userstatus" :style="{color:scope.row.Hoststatus ==='in'? 'green' : '#606266'}">{{ scope.row.Hoststatus ==='in'?"是":"否" }}</span>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-pagination
          layout="prev, pager, next"
          prev-text="上一页"
          next-text="下一页"
          class="pagination"
          :page-count="dialog.pagetotal"
          :current-page.sync="dialog.page"
          @current-change="dialogChange(dialog.companyname)">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>


<script>
import Ax from 'axios';
export default {
  name: "company",
  data() {
    return {
      loading: null,
      search: "",
      type: "1",
      dialogFormVisible:false,
      formLabelWidth:"120px",
      cur:NaN,
      page:1,
      pagetotal:1,
      list:[],
      dialog:{
        isShow:false,
        show: 2,
        page: 1,
        dialogList:[],
        companyname:"",
        inamount:"",
        totalamount:"",
        pagetotal: 1
      },
      temp:{},
      lastTime: null,
      cancel: null
    };
  },
  computed:{
    nav() {
      switch(this.$route.params.type) {
        case "1":
          return '所有公司';
        case "2":
          return '试用期中的公司';
        case "3":
          return '即将到期公司';
        case "4":
          return '已过期公司';
        case "5":
          return '今日新增公司';
        case "6":
          return '查询结果';
      }
    }
  },
  methods: {
    goSearch() {
      if(!this.search) return;
      this.$router.push('/home/company/6');
      this.searchData();
    },
    searchData() {
      this.list = [];
      const data = {
        page: this.page
      }
      this.cancelToken();
      this.$axios.searchCorp({...data,name:this.search}, {cancelToken: this.cancel.token}).then(res => {
        this.list = res.companyinfo;
      }).catch(this.reject);
    },
    deleteRow(index, arr) {
      const data = {
        companyname: arr[index].companyname
      }
      this.loading = this.$loading();
      this.$axios.deleteCorp(data).then( res => {
        this.$message({
          message: '删除成功！',
          type: 'success'
        });
        arr.splice(index, 1);
      })
      .catch(this.reject)
      .finally(() => {
        this.loading.close();
      })
    },
    edit(i) {
      this.cur = i;
      this.temp = {...this.list[this.cur]};
      this.temp.oldcompanyname = this.temp.companyname;
      if(!this.temp.disable) this.$set(this.temp, 'disable', "")
      this.dialogFormVisible = true;
    },
    submit() {
      const data = {
        oldcompanyname:this.temp.oldcompanyname,
        newcompanyname:this.temp.companyname,
        companyemail:this.temp.adminemail,
        mark:this.temp.companymark,
        disable:this.temp.disable
      }
      
      this.loading = this.$loading();
      this.$axios.companymanager(data).then( res => {
        this.$set(this.list,this.cur,{...this.temp});
        this.dialogFormVisible = false;
        this.$message({
          message: '提交成功！',
          type: 'success'
        });
      })
      .catch(this.$message.error)
      .finally(()=> {
        this.loading.close();
      });
    },
    change() {
      this.init();
    },
    dialogChange(companyname, show) {
      this.dialog.companyname = companyname;
      this.dialog.dialogList = [];
      if(show) this.dialog.show = show;
      
      const api = this.dialog.show === 1? this.$axios.companymembers : this.$axios.companyhosts
      const data = {
        companyname,
        page: this.dialog.page
      }
      this.dialog.isShow = true;
      api(data).then(res => {
        this.dialog.dialogList = this.dialog.show === 1? res.userinfo : res.totalhosts;
        this.dialog.inamount = res.inamount;
        this.dialog.totalamount = res.totalamount;
        this.dialog.pagetotal = res.pagetotal;
      }).catch(this.$message.error);
    },
    closeDialog(done) {
      this.dialog.isShow = false;
      this.dialog.companyname = '';
      this.dialog.page = 1;
      done();
    },
    pickTime(e) {
      e.companyexpire+=86399000;  //需求是到选择的那天的23点59分59秒
      const data = {
        companyname: e.companyname,
        expiredate: e.companyexpire
      }
      this.loading = this.$loading();
      this.$axios.companyexpire(data).then(res => {
        this.$message({
          message: '修改成功！',
          type: 'success'
        });
      })
      .catch(error => {
        e.companyexpire = this.lastTime;
        this.$message.error(error);
      })
      .finally(()=>{
        this.loading.close();
      });
    },
    reject(error) {
      typeof error === 'string' && this.$message.error(error);
      console.log(error);
    },
    cancelToken() {
      this.cancel && this.cancel.cancel('在调用接口之前先取消之前调用响应还没回来的接口');
      this.cancel = Ax.CancelToken.source();
    },
    init() {
      this.list = [];
      const data = {
        page: this.page
      }
      const callback = res => {
        this.list = res.companyinfo;
        this.pagetotal = res.pagetotal;
      }
      this.cancelToken();
      switch(this.type) {
        case "1":
          // '所有公司';
          this.$axios.allCorp(data, {cancelToken: this.cancel.token}).then(callback).catch(this.reject);
          break;
        case "2":
          // '试用期中的公司';
          this.$axios.tryouts(data, {cancelToken: this.cancel.token}).then(callback).catch(this.reject);
          break;
        case "3":
          // '即将到期公司';
          this.$axios.expiringcompanys(data, {cancelToken: this.cancel.token}).then(callback).catch(this.reject);
          break;
        case "4":
          // '已过期公司';
          this.$axios.expiredcompanys(data, {cancelToken: this.cancel.token}).then(callback).catch(this.reject);
          break;
        case "5":
          // '今日新增公司';
          this.$axios.newcompanys(data, {cancelToken: this.cancel.token}).then(callback).catch(this.reject);
          break;
        case "6":
        // 查询结果
      }
    },
    format(row, column) {
      return this.$formatTime(row.companyexpire, '-').split(' ')[0];
    },
    oprole(row, column) {
      switch(row.userallinfo.oprole) {
        case "3":
          return "运维人员";
        case "4":
          return "管理员与审核员";
        case "6":
          return "审核员";
      }
    },
    userstatus(userstatus) {
      switch(userstatus) {
        case "register":
          return "已关联";
        case "manual":
          return "未关联";
      }
    }
  },
  // watch:{
  //   "dialogFormVisible" : function (n, o) {
  //     if(n === false) this.temp = {};
  //   }
  // },
  created() {
    this.type = this.$route.params.type;
    this.page = 1;
    this.init();
  },
  beforeRouteUpdate(to, from, next) {
    this.type = to.params.type
    this.page = 1;
    this.type != "6" && this.init();
    next();
  }
};
</script>

<style scoped lang="scss">
.handle {
  border-bottom: 1px solid #e4e7ed;
  padding-bottom: 10px;
}
.handle .search {
  display: flex;
  align-items: center;
  padding: 20px 10px;
  .el-input {
    width: 180px;
    margin: 0 20px;
  }
  span {
    font-size: 16px;
  }
}
.table {
  padding: 30px 0;
  /deep/ .el-date-editor.el-input {
    width: auto;
    display: inline-block;
    position: relative;
    &:before {
      content:"选择日期";
      display: block;
      width: auto;
      height: 24px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 12px;
      color: #409EFF;
      cursor: pointer;
      // pointer-events:none;
    }
    span {
      display: none;
    }
    input {
      display: inline-block;
      outline: none;
      border: none;
      background-color: rgba(255,255,255,0);
      color:rgba(255,255,255,0);
      position: relative;
      cursor: pointer;
    }
  }
  .handle {
    color: #409EFF;
    cursor: pointer;
  }
}
.content {
  .item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    span {
      flex: 1;
    }
    .name {
      flex: 6;
    }
  }
}
.pagination {
  display: flex;
  justify-content: flex-end;
}
.header {
  border-bottom: 1px solid #f2f6fc;
  padding-bottom: 20px;
}
.header /deep/ .el-breadcrumb {
  display: flex;
  align-items: center;
}
.header /deep/ .first span{
  height: 45px;
  font-family: "微软雅黑 Bold", "微软雅黑 Regular", 微软雅黑;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: rgb(50, 197, 210);
  &:hover {
    height: 45px;
    font-family: "微软雅黑 Bold", "微软雅黑 Regular", 微软雅黑;
    font-weight: 700;
    font-style: normal;
    font-size: 18px;
    color: rgb(50, 197, 210);
    cursor: pointer;
  }
}
</style>



