<template>
  <div id="backend">
    <div class="container">
      <router-link class="item" tag="div" to="/home/company/5">
        <div class="count">{{info.new_companys}}</div>
        <div class="title">今日新增公司</div>
      </router-link>
      <router-link class="item" tag="div" to="/home/company/3">
        <div class="count">{{info.expiring_companys}}</div>
        <div class="title">即将到期公司</div>
      </router-link>
      <router-link class="item" tag="div" to="/home/company/2">
        <div class="count">{{info.try_companys}}</div>
        <div class="title">试用期中的公司</div>
      </router-link>
      <router-link class="item" tag="div" to="/home/company/1">
        <div class="count">{{info.total_companys}}</div>
        <div class="title">公司总数量</div>
      </router-link>
      <router-link class="item" tag="div" to="/home/user/5">
        <div class="count">{{info.new_users}}</div>
        <div class="title">今日新增用户</div>
      </router-link>
      <router-link class="item" tag="div" to="/home/user/1">
        <div class="count">{{info.total_users}}</div>
        <div class="title">用户总数量</div>
      </router-link>
    </div>
  </div>
</template>


<script>
export default {
  name: 'backend',
  data() {
    return {
      info:{
        
      }
    }
  },
  methods:{

  },
  created(){
    this.$axios.index().then(res => {
      if(Object.prototype.toString.call(res).slice(8,-1) === "Object"){
        this.info = res;
      }
    }).catch(this.$message.error);
    
  }
}
</script>

<style scoped lang="scss">
#backend {
  display: flex;
}
.container {
  padding: 0 50px;
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .item {
    width: 300px;
    height: 142px;
    margin-bottom: 30px;
    background-color: #409EFF;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    .count {
      min-height: 37px;
      margin-bottom: 20px;
      font-size: 32px;
      color: #fff;
      font-family: 'Arial Negreta', 'Arial Normal', 'Arial';
      font-weight: 700;
    }
    .title {
      font-family: 'Arial Normal', 'Arial';
      font-weight: 400;
      font-size: 14px;
      color: #fff;
    }
    &:nth-child(n+5) {
      background-color: green;
    }
  }
}
</style>



