<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  created() {
    !localStorage.getItem('botToken') && this.$router.replace('/login');
  }
}
</script>

<style lang="scss">
@import "./assets/sass/style.scss";
#app {
  height: 100%;
  width: 100%;
}
</style>



