<template>
  <div id="login">
    <el-main>
      <div class="main">
        <div class="login">
          <div class="username input">
            <span>用户名</span>
            <el-input v-model="username" placeholder="请输入用户名"  @keyup.enter.native="check"></el-input>
          </div>
          <div class="password input">
            <span>密码</span>
            <el-input v-model="password" type="password" placeholder="请输入密码"  @keyup.enter.native="check"></el-input>
          </div>
          <div class="button">
            <el-button type="primary" @click="check">登录</el-button>
          </div>
        </div>
      </div>
    </el-main>
    <!-- <el-dialog
      title="登录失败"
      :visible.sync="dialogVisible"
      width="30%">
      <span>{{failmsg}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false" id="confirm">确 定</el-button>
      </span>
    </el-dialog> -->
  </div>
</template>


<script>
export default {
  name: "login",
  data() {
    return {
      username: "",
      password: "",
      failmsg: "",
      loading:null
    };
  },
  methods: {
    check(e) {
      e.srcElement.blur();
      e.target.blur();
      this.loading = this.$loading();
      this.$axios
        .login({ username: this.username, password: this.password })
        .then(res => {
          this.loading.close();
          if (res.status != 0) throw res.msg;
          localStorage.setItem("botToken", res.token);
          this.$router.replace("/home");
        })
        .catch(e => {
          this.loading.close();
          this.$message.error(e);
          return;
        });
    }
  },
  created() {
    localStorage.removeItem("botToken");
  }
};
</script>

<style scoped lang="scss">
#login {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: relative;
}
.main {
  .login {
    width: 300px;
    height: 200px;
    background-color: #fff;
    border-radius: 5px;
    padding: 20px 10px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .input {
      display: flex;
      align-items: center;
      padding: 20px 10px;
      span {
        flex: 1;
      }
      .el-input {
        flex: 2;
        width: 180px;
        margin: 0 20px;
      }
    }
    .button {
      text-align: center;
    }
  }
}
</style>



