import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/Login'
import Reset from '@/views/Login/reset'
import Home from '@/views/Home'
import Backend from '@/views/Home/backend'
import Company from '@/views/Home/company'
import User from '@/views/Home/user'
import Setting from '@/views/Home/setting'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:"/home"
    },
    {
      path: '/home',
      component: Home,
      children:[
        {
          path:"",
          component:Backend
        },
        {
          path:"company/:type",
          component:Company
        },
        {
          path:"user/:type",
          component:User
        },
        {
          path:"setting",
          component:Setting
        },
      ]
    },
    {
      path:"/login",
      component:Login
    },
    {
      path:"/reset",
      component:Reset
    }
  ]
})



