<template>
  <div id="setting">
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item class="first">参数设置</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="handle">
      <div class="title">设置产品各种参数或数据</div>
      <div class="form">
        <div>
          <span class="question">正式使用的产品到期提前多少天提醒？</span>
          <div class="right">
            <el-input v-model="expire" type="number" placeholder="请输入天数"></el-input>
            <span>天</span>
          </div>
        </div>
        <div>
          <span class="question">客服电话</span>
          <div class="right">
            <el-input v-model="customerservice" type="tel" placeholder="请输入客服电话" class="phone"></el-input>
          </div>
        </div>
      </div>
    </div>
    <div class="button">
      <el-button type="primary" @click="submit">保存</el-button>
    </div>
  </div>
</template>


<script>
export default {
  name: "setting",
  data() {
    return {
      expire: 0,
      customerservice: "",
      loading:null
    };
  },
  methods: {
    submit() {
      const data = {
        expire: this.expire,
        customerservice: this.customerservice
      }
      this.loading = this.$loading();
      this.$axios.editConfigs(data).then(res => {
        if(res.status != 0) throw res.msg;
        this.loading.close();
        this.$message({
          message: '保存成功！',
          type: 'success'
        });
      }).catch(e=> {
        this.loading.close();
        console.log(e);
        this.$message.error(e);
      })

    },
    init() {
      this.$axios.configs().then(res => {
        this.expire = res.expire;
        this.customerservice = res.customerservice;
      }).catch(this.$message.error);
    }
  },
  created() {
    this.init();
  }
};
</script>

<style scoped lang="scss">
.handle {
  .title {
    margin: 30px 0;
    font-family: "微软雅黑";
    font-weight: 410;
    font-style: normal;
    font-size: 16px;
  }
  .form {
    padding: 30px 15px;
    background-color: #ebeef5;
    border-radius: 5px;
    font-family: 微软雅黑;
    font-weight: 410;
    font-style: normal;
    font-size: 16px;
    > div {
      display: flex;
      align-items: center;
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0;
      }
      .el-input {
        width: 180px;
        margin: 0 20px;
      }
      .question {
        flex: 1;
      }
      .right {
        flex: 2;
        .phone {
          width: 270px;
        }
      }
    }
  }
}
.header {
  border-bottom: 1px solid #f2f6fc;
  padding-bottom: 20px;
}
.header /deep/ .el-breadcrumb {
  display: flex;
  align-items: center;
}
.header /deep/ .first span {
  height: 45px;
  font-family: "微软雅黑 Bold", "微软雅黑 Regular", 微软雅黑;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: rgb(50, 197, 210);
  &:hover {
    height: 45px;
    font-family: "微软雅黑 Bold", "微软雅黑 Regular", 微软雅黑;
    font-weight: 700;
    font-style: normal;
    font-size: 18px;
    color: rgb(50, 197, 210);
    cursor: pointer;
  }
}

.button {
  margin-top: 30px;
}
</style>



