<template>
  <div id="user">
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home/user/1' }" class="first">用户管理</el-breadcrumb-item>
        <el-breadcrumb-item v-if="$route.params.type != '1'">{{nav}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="handle">
      <div class="search" v-if="$route.params.type == 1 || $route.params.type == 6">
        <span>公司名称</span>
        <el-input v-model="search.companyname" placeholder="请输入公司名称"></el-input>
        <span>手机号</span>
        <el-input v-model="search.mobile" placeholder="请输入手机号"></el-input>
        <el-button type="primary" @click="goSearch">查询</el-button>
      </div>
      <div class="select" v-if="$route.params.type == 1">
        <el-radio-group v-model="type" @change="$router.push(`/home/user/${type}`)">
          <el-radio-button label=1>所有用户</el-radio-button>
          <el-radio-button label=2>未关联公司</el-radio-button>
          <el-radio-button label=3>停用</el-radio-button>
        </el-radio-group>
      </div>
    </div>
    <div class="table">
      <el-table
        :data="list"
        border
        style="width: 100%">
        <el-table-column
          prop="companyname"
          label="所属公司"
          align="center">
        </el-table-column>
        <el-table-column
          prop="userallinfo.username"
          label="用户姓名"
          align="center">
        </el-table-column>
        <el-table-column
          prop="userallinfo.mobile"
          label="手机号码"
          align="center">
        </el-table-column>
        <el-table-column
          prop="userallinfo.logintime"
          label="上次登录"
          align="center">
        </el-table-column>
        <el-table-column
          prop="userallinfo.userdisable"
          label="状态"
          align="center">
          <template slot-scope="scope">
            <span class="userstatus" :style="{color:scope.row.userallinfo.userdisable ==='true'? 'red' : '#606266'}">{{ scope.row.userallinfo.userdisable ==='true'? '停用' : '启用' }}</span>
          </template>
        </el-table-column>
        <el-table-column
        prop="members"
          label="操作"
          align="center">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="disableUser(scope)">{{ scope.row.userallinfo.userdisable === 'true'? "启用" : "禁用"}}</el-button>
            <el-button type="text" size="small" @click="deleteRow(scope.$index, list)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      layout="prev, pager, next"
      prev-text="上一页"
      next-text="下一页"
      class="pagination"
      :total="pagetotal"
      :current-page.sync="page"
      @current-change="change">
    </el-pagination>
  </div>
</template>


<script>
import Ax from 'axios';
export default {
  name: "user",
  data() {
    return {
      search: {
        companyname:"",
        mobile:""
      },
      type: "1",
      dialogFormVisible:false,
      formLabelWidth:"120px",
      cur:NaN,
      page:1,
      pagetotal: 1,
      list: [],
      temp:{},
      cancel: null
    };
  },
  computed:{
    nav() {
      switch(this.$route.params.type) {
        case "1":
          return '所有用户';
        case "2":
          return '未关联公司';
        case "3":
          return '停用';
        case "5":
          return '今日新增用户';
        case "6":
          return '查询结果';
      }
    }
  },
  methods: {
    goSearch() {
      this.$router.push('/home/user/6');
      this.searchData();
    },
    searchData() {
      this.list = [];
      const data = {
        page: this.page
      }
      this.cancelToken();
      this.$axios.searchUser({...data, ...this.search}, {cancelToken: this.cancel.token}).then(res => {
        this.list = res.userinfo;
        this.list.forEach(e => {
          e.userallinfo.logintime = this.$formatTime(e.userallinfo.logintime).replace(/\//g, '-');
        })
      }).catch(this.reject);
    },
    deleteRow(index, arr) {
     const data = {
        userid: arr[index].userallinfo.userid
      }
      this.loading = this.$loading();
      this.$axios.deleteUser(data).then( res => {
        this.$message({
          message: '删除成功！',
          type: 'success'
        });
        arr.splice(index, 1);
      })
      .catch(this.reject)
      .finally(() => {
        this.loading.close();
      })
    },
    disableUser(scope) {
      const info = scope.row.userallinfo;
      const disable = info.userdisable === 'true' ? 'false' : 'true';
      const data = {
        userdisable: disable,
        userid: info.userid
      }
      this.loading = this.$loading();
      this.$axios.disableUser(data).then( res => {
        this.$message({
          message: disable === 'true' ? '禁用成功' : '启用成功',
          type: 'success'
        });
        info.userdisable = disable;
        this.$set(this.list, scope.$index,this.list[scope.$index]);
      })
      .catch(this.$message.error)
      .finally(() => {
        this.loading.close();
      })
    },
    change() {
      this.init();
    },
    reject (error) {
      typeof error === 'string' && this.$message.error(error);
      console.log(error);
    },
    cancelToken() {
      this.cancel && this.cancel.cancel('在调用接口之前先取消之前调用响应还没回来的接口');
      this.cancel = Ax.CancelToken.source();
    },
    init() {
      this.list = [];
      const data = {
        page: this.page
      }
      const callback = res => {
        this.list = res.userinfo;
        this.pagetoal = res.pagetotal;
        this.list.forEach(e => {
          e.userallinfo.logintime = this.$formatTime(e.userallinfo.logintime).replace(/\//g, '-');
        })
      }
      this.cancelToken();
      switch(this.type) {
        case "1":
          // '所有用户';
          this.$axios.users(data, {cancelToken: this.cancel.token}).then(callback).catch(this.reject);
          break;
        case "2":
          // '未关联公司';
          this.$axios.tourist(data, {cancelToken: this.cancel.token}).then(callback).catch(this.reject);
          break;
        case "3":
          // '停用';
          this.$axios.expiringcompanys(data, {cancelToken: this.cancel.token}).then(callback).catch(this.reject);
          break;
        case "5":
          // '今日新增用户';
          this.$axios.newusers(data, {cancelToken: this.cancel.token}).then(callback).catch(this.reject);
          break;
        case "6":
        // 查询结果
      }
    }
  },
  // watch:{
  //   "dialogFormVisible" : function (n, o) {
  //     if(n === false) this.temp = {};
  //   }
  // },
  created() {
    this.type = this.$route.params.type;
    this.page = 1;
    this.init();
  },
  beforeRouteUpdate(to, from, next) {
    this.type = to.params.type
    this.page = 1;
    this.type != "6" && this.init();
    next();
  }
};
</script>

<style scoped lang="scss">
.handle {
  border-bottom: 1px solid #e4e7ed;
  padding-bottom: 10px;
}
.handle .search {
  display: flex;
  align-items: center;
  padding: 20px 10px;
  .el-input {
    width: 180px;
    margin: 0 20px;
  }
  span {
    font-size: 16px;
  }
}
.table {
  padding: 30px 0;
  /deep/ .el-date-editor.el-input {
    width: auto;
    display: inline-block;
    position: relative;
    &:before {
      content:"选择日期";
      display: block;
      width: auto;
      height: 24px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 12px;
      color: #409EFF;
      cursor: pointer;
      // pointer-events:none;
    }
    span {
      display: none;
    }
    input {
      display: inline-block;
      outline: none;
      border: none;
      background-color: rgba(255,255,255,0);
      color:rgba(255,255,255,0);
      position: relative;
      cursor: pointer;
    }
  }
}
.content {
  .item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .name {
      flex: 1;
    }
  }
}
.pagination {
  display: flex;
  justify-content: flex-end;
}
.header {
  border-bottom: 1px solid #f2f6fc;
  padding-bottom: 20px;
}
.header /deep/ .el-breadcrumb {
  display: flex;
  align-items: center;
}
.header /deep/ .first span{
  height: 45px;
  font-family: "微软雅黑 Bold", "微软雅黑 Regular", 微软雅黑;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: rgb(50, 197, 210);
  &:hover {
    height: 45px;
    font-family: "微软雅黑 Bold", "微软雅黑 Regular", 微软雅黑;
    font-weight: 700;
    font-style: normal;
    font-size: 18px;
    color: rgb(50, 197, 210);
    cursor: pointer;
  }
}
</style>



