import loadingCOM from "../components/loading";
import {api} from "../api"
export default {
  install(Vue, options) {
    //loading组件封装 Start
    let load = null;
    let ele = Vue.extend(loadingCOM);
    let loading = () => {
      if (!load) {
        load = new ele().$mount();
        document.body.appendChild(load.$el);
      }
      load.show();
    };
    loading.hide = () => {
      load && load.hide();
    };
    // Vue.prototype.$loading = loading;
    //loading组件封装 End

    //获取查询字符串
    Vue.prototype.$getQueryString = e => {
      let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      let r = window.location.search.substr(1).match(reg);
      if (r !== null) return unescape(r[2]);
      return null;
    };

    //时间戳转换成字符串时间
    Vue.prototype.$formatTime = (e, split='/') => {
      if (!e) return '';
      let time = new Date(e);
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      let date = time.getDate();
      let hour = time.getHours();
      let min = time.getMinutes();
      let sec = time.getSeconds();
      
      return `${year}${split}${month > 9 ? month : "0" + month}${split}${
        date > 9 ? date : "0" + date
      } ${hour > 9 ? hour : "0" + hour}:${min > 9 ? min : "0" + min}:${
        sec > 9 ? sec : "0" + sec
      }`;
    };

    //绑定api
    Vue.prototype.$axios = api;
  }
};



