<template>
  <div id="home">
    <el-header>
      <div class="header">
        <img src="/static/images/u2.png" alt="" @click="isCollapse = !isCollapse">
        <div class="user">
          <el-dropdown @command="handle">
            <span class="el-dropdown-link">
              超级管理员<i class="el-icon-caret-bottom" style="color:#32c5d2"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="signout">退出登录</el-dropdown-item>
              <el-dropdown-item command="password" divided>修改密码</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </el-header>
    <el-container>
      <el-menu :default-active="path" router class="el-menu-vertical-demo" :collapse="isCollapse" @select="selectPath">
        <el-menu-item index="/home">
          <div class="el-icon-home"><img src="/static/images/home.png" alt=""></div>
          <span slot="title">后台首页</span>
        </el-menu-item>
        <el-menu-item index="/home/company/1">
          <div class="el-icon-home"><img src="/static/images/corp.png" alt=""></div>
          <span slot="title">公司管理</span>
        </el-menu-item>
        <el-menu-item index="/home/user/1">
          <div class="el-icon-home"><img src="/static/images/user.png" alt=""></div>
          <span slot="title">用户管理</span>
        </el-menu-item>
        <el-menu-item index="/home/setting">
          <div class="el-icon-home"><img src="/static/images/set.png" alt=""></div>
          <span slot="title">参数设置</span>
        </el-menu-item>
      </el-menu>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </div>
</template>


<script>
export default {
  name: "home",
  data() {
    return {
      isCollapse: false
    };
  },
  computed:{
    path() {
      const type = this.$route.params.type
      return type? this.$route.path.slice(0,-1)+'1':this.$route.path
    }
  },
  methods: {
    handle(e) {
      this[e]();
    },
    signout() {
      localStorage.removeItem('botToken');
      this.$router.replace('/login');
    },
    password() {
      this.$router.push('/reset');
    },
    selectPath() {
      // alert(1)
    }
  },
  created() {
    
  }
};
</script>

<style scoped lang="scss">
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}

#home {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  /deep/ section {
    padding: 20px;
    background-color: #e9ebf4;
    > .el-menu-vertical-demo.el-menu,
    > main {
      background-color: #fff;
    }
    > .el-menu-vertical-demo.el-menu {
      border-radius: 10px;
      margin-right: 20px;
    }
    >main {
      flex-basis:0;
    }
  }
}

.header {
  padding:0 20px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  img,.user {
    cursor: pointer;
  }
  .user {
    font-size: 14px;
    font-weight: 400;
  }
}

div[class^="el-icon-"] img {
  width: 18px;
  height: 18px;
}
</style>



