<template>
  <transition name="mask">
    <div class="loading" v-show="showMask">
        <img src="/static/images/loading@2x.png" alt="">
        <!-- <i class="el-icon-loading"></i> -->
    </div>
  </transition>
</template>

<script>
export default {
  name: "loading",
  data() {
    return {
      showMask: false
    };
  },
  methods: {
    show() {
      this.showMask = true;
    },
    hide() {
      this.showMask = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../assets/sass/style";
.loading {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  // background: rgba(0, 0, 0, 0.4);
  z-index: 999;
  img {
    animation: loading 1s linear infinite;
    width: 20px;
  }
}

@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.mask-leave-to {
  opacity:0;
}

.mask-enter-active,.mask-leave-active {
  transition:opacity .2s;
}
</style>



